@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=VT323&display=swap');

// font-family: 'Montserrat', sans-serif;

*, *:before, *:after { box-sizing: border-box; -moz-box-sizing: border-box; image-rendering: -webkit-optimize-contrast;}
img{max-width: 100%; vertical-align: top; image-rendering: -webkit-optimize-contrast;}
body { margin: 0; position: relative; overflow-x: hidden; }
.clearfix { clear: both; overflow: hidden; }
a { transition: all 0.7s ease; }
img { max-width: 100%; }

